/*@import "./base/conn.less";*/
.data-times {
  position: relative;
  display: inline-block;
}
.data-times .allData {
  background: #fff;
  height: 30px;
  line-height: 28px;
  padding: 0 10px;
  width: 295px !important;
  border-radius: 2px;
  border: 1px solid #d3dbe3;
  color: #757575;
  outline: none;
}
.data-times .allData:focus {
  border: 1px solid #029be5;
  outline: none;
}
.data-times .dataList {
  display: none;
  position: absolute;
  top: 30px;
  margin-top: 10px;
  width: 209px;
  background-color: #fff;
  border: 1px solid #e6e6e6;
  padding: 14px 10px;
  z-index: 999;
  left: 50%;
  transform: translateX(-50%);
}
.data-times .dataList > ul {
  position: relative;
  border-bottom: 1px solid #e6e6e6;
  padding-bottom: 10px;
}
.data-times .dataList > ul:before {
  content: "";
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid #e2e2e2;
  position: absolute;
  top: -24px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1001;
}
.data-times .dataList > ul:after {
  content: "";
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid #fff;
  position: absolute;
  top: -23px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1001;
}
.data-times .dataList > ul li {
  width: 100%;
  text-align: center;
  height: 30px;
  line-height: 30px;
  border-radius: 2px;
}
.data-times .dataList > ul li a {
  display: inline-block;
  width: 100%;
  height: 100%;
  color: #4c4c4c;
}
.data-times .dataList > ul li a:hover {
  color: #fff;
  background-color: #029be5;
}
.data-times .dataList .timeCustom .custom-title {
  height: 33px;
  line-height: 33px;
  font-weight: bold;
  font-size: 14px;
  color: #029be5;
  text-align: center;
}
.data-times .dataList .timeCustom input {
  width: 168px;
  height: 34px;
  margin-left: 5px;
  border: 1px solid #DEDBE3;
}
.data-times .dataList .timeCustom .custom-data {
  margin-bottom: 9px;
}
.data-times .dataList .timeCustom .custom-data input {
  background: url(../images/date.png) no-repeat 97% center;
  background-color: white;
  outline: none;
}
.data-times .dataList .timeCustom .custom-data input:focus {
  outline: none;
  border: 1px solid #029be5;
}
.data-times .dataList .timeCustom .custom-btn {
  display: flex;
  justify-content: space-between;
  padding-left: 17px;
  padding-top: 6px;
}
.data-times .dataList .timeCustom .custom-btn > a {
  height: 28px;
  line-height: 28px;
  padding: 0 15px;
  font-size: 14px;
  letter-spacing: 1px;
  color: #666;
  background: linear-gradient(#fff, #f8f8f8);
  border: 1px solid #DEDBE3;
  border-radius: 2px;
}
